<script>
import axios from "axios"
export default {
	data() {
		return {
			title: "",
			body: "",
			submitting: false,
		}
	},
	methods: {
		async handleSubmit() {
			this.submitting = true
			const url = "http://localhost:4000/articles/new-article"
			try {
				const response = await axios({
					url,
					method: "post",
					data: {
						title: this.title,
						body: this.body,
					},
					headers: {
						"Content-Type": "application/json",
					},
				})

				if (response.data.acknowledged) {
					this.title = ""
					this.body = ""
				}

				this.submitting = false
			} catch (error) {
				console.error(error)
			}
		},
	},
}
</script>

<template>
	<div>
		<h2>New Post</h2>
		<div v-if="submitting">Submitting</div>

		<form @submit.prevent="handleSubmit" v-else>
			<label for="title"> Title</label>
			<br />
			<input name="title" id="title" v-model="title" />
			<br />
			<label for="title"> Body </label>
			<br />
			<textarea rows="10" name="body" id="body" v-model="body" />
			<br />
			<input type="submit" value="Submit" />
		</form>
	</div>
</template>

<style scoped>
form {
	text-align: left;
}
</style>
